<script>
export default{
  name:'app-header',
  props:{show:{ default:true} },
  data: function(){
    return {
      dropdown:false,
      main: [
        {title:'Home', src:'/'},
        {title:'Courses', sub:[{title:'Free Courses', src:'/courses'}, {title:'Math', src:'/courses'},{title:'All Courses', src:'/courses'}]},
        {title:'Systems', sub:[{title:'MCQ', src:'/tools/mcq'}]},
        {title:'Community', sub:[{title:'Stories', src:'/community'}, {title:'Forums', src:'/community'}]},
        {title:'About', sub:[{title:'About Us', src:'/about'}, {title:'Contact Us', src:'/about'}]},
            ]
    }
  },
  methods:{
    navigate:function(link){
      this.dropdown=false;
      this.$router.push(link)
    },
    showMenu:function(){
      this.dropdown=!this.dropdown;
      document.querySelector('[mobile-dropdown]').scrollTo(0);
    }
  }

}

